@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
body, html {
    wdith: 100%;
    height: 100%;
    margin: 0px;
    font-family: 'Varela Round', sans-serif;
}

.nav-menu{
	background-color: #010724;
	position: fixed;
	top: 60px;
	width: 100vw;
	height: 100vh;
	left: -100vw;
	opacity: 0;
	z-index: 100;
	transition: .5s;
}
.active{
	left: 0px;
	opacity: 1;
}
a{
	color: white;
}
.navbar{
	width: 100%;
	height: 60px;
	background-color: transparent;
	color: white;
	font-size: 25px;
	line-height: 60px;
	position: fixed;
	z-index: 1000;
}
.navbar-container{
	width: 70%;
	margin: auto;
}
.menu-icon{
	display: inline;
	float: right;
}
.menu-items{
	width: 70%;
	margin: auto;
}
.nav-menu-container{
	width: 70%;
	height: 100%;
	margin: auto;
}
ul .menus-item{
	list-style: none;
	font-size: 15px;
	text-align: center;
}
.menus-item:hover{
	background-color: white;
}
.menus-item:hover a{
	color: #010724;
}
.menus-item-signup, .menus-item-logout{
	font-weight: bolder;
}
.nav-menu-desktop{
	display: none;
	float: right;
	position: relative;
	top: -63px;
}
.show{
	display: inline;
}
ul .menus-item-desktop{
	display: inline;
	font-size: 15px;
	margin-left: 10px;
}
.menus-item-desktop-signup{
	border: 1px solid white;
	padding: 5px 10px;
	border-radius: 5px;
	background-color: white;
	color: #010724;
	font-weight: bold;
}
.menus-item-desktop-logout{
	border: 1px solid white;
	padding: 5px 10px;
	border-radius: 5px;
	background-color: white;
	color: #010724;
	font-weight: bold;
}
#profile-icon i{
	position: relative;
	top: 4px;
	font-size: 25px;
}
#profile-icon{
	margin-left: 0px;
	margin-right: 0px;
}

@media screen and (max-width: 760px){
	.navbar-container{
		width: 90%;
		margin: auto;
	}

}

video{
	object-fit: cover;
	width: 100%;
	height: 100vh;
	position: absolute;
	z-index: -1;
}
.showcase{
	width: 100%;
	height: 100vh;
	background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url("/static/images/background.png");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}
.showcase-text{
	text-align: center;
	width: 60%;
	position: relative;
	top: 55%;
	left: 50%;
	transform: translate(-50%, -65%);
