	font-size: 12px;
	margin-bottom: 35px;
	text-shadow: 1px 1px 1px black;
}
a{
	text-decoration: none;
}
.showcase-btn-primary{
	background-color: white;
	border: 2px solid white;
	color: #010724;
	font-weight: bold;
	padding: 10px 20px;
	cursor: pointer;
	margin-right: 10px;
}
.showcase-btn-secondary{
	border: 2px solid white;
	font-weight: bold;
	padding: 10px 20px;
	cursor: pointer;
}

@media screen and (max-width: 960px) {
